<template>
  <Loading
    :class="{
      'content--hide': !showLoading,
      'content--show': showLoading,
    }"
    v-if="displayLoading"
  />
  <div
    :class="{
      'markdown-content': true,
      'content--hide': !showContent,
      'content--show': showContent,
    }"
    v-if="displayContent"
  >
    <div class="markdown-body" v-html="htmlContent"></div>
  </div>
</template>

<script>
import { ref, onMounted } from "vue";
import { scrollToTargetId, timeoutPromise } from "../utils";
import useHashChange from "../hooks/useHashChange";
import md from "markdown-it";
import Loading from "../components/Loading";
import data from "!raw-loader!../assets/md/about.md";

let showLoading = ref(true);
let showContent = ref(false);
let displayLoading = ref(true);
let displayContent = ref(false);
const htmlContent = ref("");

export default {
  name: "About",
  components: { Loading },
  setup() {
    onMounted(async () => {
      if (showLoading.value) {
        const mdConfig = md({
          html: true,
          linkify: true,
          typographer: true,
        });
        htmlContent.value = mdConfig.render(`${data}`);

        await timeoutPromise(() => {
          // 等待 v-if 效果
          showLoading.value = false;
        }, 1000)
          .then(() => {
            return timeoutPromise(() => {
              // 等待loading動畫結束
              displayLoading.value = false;
              displayContent.value = true;
            }, 500);
          })
          .then(() => {
            return timeoutPromise(() => {
              // 等待 v-if 效果
              showContent.value = true;
              // 若網址帶有 hashtag 直接進入頁面
              scrollToTargetId();
            }, 100);
          });
      } else {
        // 若使用者前往其他筆記往並使用瀏覽器往前往後切換頁面帶有 hashtag
        scrollToTargetId();
      }
    });
    // 若在同個筆記切換 hash (#???)，則使用onhashchange監聽
    useHashChange();
    return {
      showLoading,
      showContent,
      displayLoading,
      displayContent,
      htmlContent,
    };
  },
};
</script>
<style lang="scss">
@import "../style/md.scss";
.content--hide {
  opacity: 0;
}

.content--show {
  opacity: 1;
}
</style>
